.layout{width:1200px;margin:0 auto;}
footer{height:300px;background-color:#fafafa;}
footer .aboutUs{height:40px;background-color:#f2f2f2;border-bottom:2px solid #dadada}
footer .aboutUs .layout ul{float: left;display: flex;font:14px/40px "";}
footer .aboutUs .layout ul li{margin-left:10px}
footer .aboutUs .layout ul span{margin:15px 0 0 5px;width: 1px;height: 10px;background: #4e4c53;display: inline-block;}
footer .footer_t{height:150px;padding-bottom:29px;border-bottom: 1px solid #999;}
footer .footer_t img{margin:64px 0 0 92px ;width:121px;height:58px;float: left;}
footer .footer_t span{float:left;margin:30px 0 0 90px;width: 1px;height: 120px;background: #999;display: inline-block;}
footer .footer_t_c,.footer_t_r{float: left;margin:40px 0 0 55px;font-size: 12px;line-height: 18px;color: #999;}
footer .footer_t h3{margin-bottom: 3px;font-size: 13px;line-height: 19px;font-weight: bold;color: #333;}
footer .footer_b{padding-top:20px;font-size: 12px;line-height: 18px;color: #999;}