
.layout{width:1200px;margin:0 auto;}
.banner12{height:465px;background-color:#e37fb1;}
.banner12 .banner{position: relative;left:175px;width:1025px;height:465px;}
.banner12 .banner .banner1{position: absolute;}
.banner12 .banner .img{position: absolute;}
.banner12 .banner .img:nth-child(1){z-index:10}
.banner12 .banner .img:nth-child(2){z-index:9}
.banner12 .banner .img:nth-child(3){z-index:8}
.banner12 .banner .img:nth-child(4){z-index:7}
.banner12 .banner .img:nth-child(5){z-index:6}
.banner12 .banner ul{position: absolute;top:429px;height:37px;width:983px;display: flex}
.banner12 .banner ul li{width:197px;height:35px;border-bottom:1px solid #c0c3c8;border-right:1px solid #c0c3c8;background:#f9f8fb;font: 14px/35px "";text-align: center;z-index: 19;}
.banner12 .banner button{width:42px;height:37px;background:#f9f8fb url(../images/icon.png) no-repeat center;display: block;position: absolute;;left:983px;top:429px;z-index: 19;border:none;background-position:-60px 0}

.banner12 .banner .left,.right{width:60px;height:60px;position: absolute;z-index: 12;opacity: 0.7;}
.banner12 .banner .left{left:0;top:200px;background:url(../images/icon.png) no-repeat center;background-position: 0 -180px;}
.banner12 .banner .right{right:241px;top:200px;background:url(../images/icon.png) no-repeat center;background-position: 0 -60px;}
.banner12 .banner .zb img{width:242px;height:380px;position: absolute;z-index: 12;right:0;top:40px;background-color: rgb(255, 111, 111,0);}