.layout{width:1200px;margin:0 auto;}
.headerNav{height:40px;background-color:#fbfbfb;border-bottom:2px solid #dadada}
.headerNav .layout>div{float:right;font:13px/17px "";display:flex;padding-top:10px;}
.headerNav .layout>div>a{margin:0 3px}
.headerNav .layout>div>span{margin:3px 3px 0 0;width: 1px;height: 10px;background: #e2e1e0;}
header{height:110px;background-color:#fff;border-bottom:2px solid #e0e0e0;overflow: hidden;}
header .logo{display:block;background: url(../images/logo.png);width:98px;height:53px;margin:36px 0 0 39px;float: left;}
header .txt{width:435px;height:45px;box-sizing:border-box;border:2px solid #f43445;float: left;margin:30px 0 0 78px;font: 14px/18px "";text-indent:1em;}
header .btn1{width:45px;height:45px;border:2px solid #f43445;background:#f43142 url(../images/fdj.png) no-repeat;float: left;margin-top:30px;}
header>.layout>div{width:200px;height:18px;float: left;margin:45px 0 0 22px;}
header>.layout>div>a>em{font: 14px/18px "";}
header>.layout>div>span{margin:3px 3px 0 0;width: 1px;height: 10px;background: #e2e1e0;display: inline-block;}
header .ps{width:160px;height:46px;background:url(../images/gjfs.png);display:block;float:right;margin-top:30px}

nav{height:52px;background-color:#fff;border-bottom:2px solid #000;position: relative;}
nav .btn2{height:52px;width:175px;;cursor: default;font:14px/37px "";border:2px solid #e0e0e0;background-color: transparent;vertical-align: middle;    background-color: rgb(233, 237, 240);float: left;}
nav .btn2 .xlg{width:16px;height:13px;background:url(../images/icon.png) no-repeat;background-position: -109px -8px;float: left;margin:15px 0 0 13px;}
nav .btn2 .cpfl{float: left;margin-left:8px}
nav>.layout>a{width:100px;display: block;float: left;font: 14px/51px "";margin-left:15px}
nav .yiji{width:175px;height:465px;background:#161223;position:absolute;top:53px;z-index: 20;display: none}
nav .yiji li{padding-left:10px;height:41px;border-bottom:1px solid #4e4c53;font: 15px/41px "";color: #fff;}
nav .yiji>li:nth-child(11){border:none}
nav .yiji>li .erji{height:465px;width:400px;background:#fff;position:absolute;top:0;left:175px;display:none}
nav .yiji>li .erji h3{font:25px/95px "";margin-left:10px;color:#000}
nav .yiji>li .erji i{position:absolute;height:2px;left:10px;right:10px;background:#f2f2f2;}
nav .yiji>li .erji .erjic{margin-top:10px;}
nav .yiji>li .erji .erjic a{font:13px/22px "";height:22px;display:block;margin-left:10px}
