.layout{width:1200px;margin:0 auto;}
main{padding:75px 0}
main h3{margin:0 auto; text-align: center;font: 44px/44px "";color: #333;text-align: center;font-weight: normal;}
main .main_a{display:flex;justify-content:center;margin-top:30px}
main .main_a .main_l,.main_r{margin-left:10px;padding: 20px;height: 332px;width:360px;border: 1px solid #b9b9b9;}
main .main_a .main_l{background: #e9f2f9;}
main .main_a .main_r{background: #fafafa;}
main .main_a .main_l p{font: 18px/18px "";color: #333;}
main .main_a .main_l ul{margin-top:100px;padding: 0 20px;}
main .main_a .main_l ul li{height:20px;font-size: 12px/14px "";margin-top:30px}
main .main_a .main_l ul li span{float: left;}
main .main_a .main_l .ck{float: right;}
main .main_a .main_l ul li b{float: right;margin-right:10px}
main .main_a .main_l .txt1{float: left;margin-left:10px}
main .main_a .main_l .PW{float: left;margin-left:22px}
main .main_a .main_l .btn0{float: right;width:60px;height:20px;background:rgb(248, 96, 76);border: 0;color:#fff}
main .main_a .main_r h4{font: 18px/18px "";color: #333;padding-bottom: 16px;}
main .main_a .main_r h5{padding: 4px 0 7px 0;font: 15px/15px "";color: #333;font-weight: normal;}
main .main_a .main_r p{height: 230px;border-bottom: 1px solid #b9b9b9;font: 13px/15px "";}
main .main_a .main_r a{text-align: center;display:block;width: 143px;border-top: 1px solid #fff;font:11px/20px "";color: #fff;background: #666;margin:20px 0 0 100px;height:20px}
