.layout{width:1200px;margin:0 auto;}
main{padding:75px 200px;width:900px;margin:0 auto;}
main h2{font:36px/36px "";color: #333;margin-bottom:30px}
main .main_t ul{border-top:2px solid #999;border-bottom:2px solid rgb(168, 168, 168)}
main .main_t ul li{height:70px;border-bottom: 1px solid #bbb;}
main .main_t ul li input{padding: 2px 5px 3px 5px;color: #4d4d4d;font-size: 12px;line-height: 14px;word-wrap: break-word;border: 1px solid #bbb;width:312px;height:30px;margin:15px 0 0 5px
}
main .main_t ul li span{font: 14px/18px "";color: #333;font-weight: normal;text-align: left;display:block;height:30px;width:250px;padding:20px;float: left;background: #fafafa;}
main .main_t ul li b{font:14px/18px "";color: #333;font-weight: normal;text-align: left;color: #4178cd;}
main .main_t ul li .ckb{width:14px;height:14px;font:14px/18px "";}
main .main_b{display: flex;justify-content: center;margin-top:30px}
main .main_b button{margin-left:20px;height: 40px;padding: 0 10px;display: block;cursor: pointer;}
main .main_b .qr{border: 1px solid #f9827d;background:#ec4640;}
main .main_b .qr a{color: #fff;font: 14px/38px "";font-weight: bold;display: block;width:162px;background:#ec4640;}
main .main_b .qx{border: 1px solid #666;background-color: transparent;}
main .main_b .qx a{color: #666;font: 14px/38px "";font-weight: bold;display: block;width:162px;background-color: transparent;}
