main{padding:75px 0;width:1200px;margin:0 auto;height:600px}
main .xq{position: relative;height:500px;border:1px solid #4e4c53}
main .xq .xq_l{float: left;}
.s_box{width: 400px;height: 400px;position: absolute;left: 50px;top: 20px;border:1px solid #eee}
.s_box img{width: 400px;height: 400px;}
.s_box span{width: 100px;height: 100px;position: absolute;left: 0;top: 0;background: rgba(200,200,200,.6);display: none;}

.b_box{width: 400px;height: 400px;position: absolute;left: 500px;top: 20px;overflow: hidden;display: none;}
.b_box img{width: 800px;height: 800px;position: absolute;}
main .xq .xq_r{float:right;}
main .xq .xq_r .xq_r_t h3{padding: 5px 0;font: 22px/28px "";font-weight: bold;color: #010101;width:400px;margin:50px 150px 0 0;}
main .xq .xq_r .xq_r_t p{color: #111;font: 28px/29px "";}
main .xq .xq_r .xq_r_b{margin:100px 30px 0 0}
main .xq .xq_r .xq_r_b span{display: block;font-weight: bold;font-size: 16px;color: #999;line-height: 48px;height: 48px;width: 200px;border: 1px solid #999;background: #fff;float: left;text-align: center}
main .xq .xq_r .xq_r_b span:nth-child(1){border-right: none;}
main .xq .xq_r .xq_r_b span:nth-child(2){border-right: none;}
main .xq .xq_r .xq_r_b span:nth-child(3){background: rgb(240, 58, 58);color: #fff;border: 1px solid rgb(240, 58, 58);}