.layout{width:1200px;margin:0 auto;}
.main{border-bottom:2px solid #dadada;padding-bottom: 35px;background:#e9edf0}
.main .h3{font:28px/36px "";font-weight: bold;color: #2d323b;}

.main .main_news{padding-top:40px;background:#e9edf0}
.main .main_news ul li:nth-child(1) a img{width:400px;height:380px;display:block;background:orange;float: left;margin-left:-95px}
.main .main_news ul li:nth-child(2) a img{width:305px;height:380px;display:block;background:pink;float:right;}
.main .main_news ul li:nth-child(3) a img{width:590px;height:190px;display:block;background:greenyellow}
.main .main_news ul li:nth-child(4) a img{width:590px;height:190px;display:block;background:blue}


.main .main_best{padding:45px 0 20px 0;background:#f4f6f8;height:1009px}
.main .layout{position: relative;}
.main .main_best h3{font:28px/36px "";font-weight: bold;color: #2d323b;padding-bottom:15px;float: left;}
.main .main_best .tjzq{float:right;}
.main .main_best .tjzq a{height: 28px;width: 40px;padding: 0 17px;margin-left: -4px;line-height: 28px;color: #666;font-size: 14px;line-height: 28px;display: block;float: left;text-align: center;border-radius:14px}
.main .main_best .tjzq span{float:left;width: 5px;height: 5px;background: #666;display: inline-block;border-radius: 50%;display: block;margin:10px}

.main .main_best ul {width: auto;top:50px;display:flex;flex-wrap:wrap;position: absolute;}
.main .main_best ul li{padding:10px 5px 0 0;width:285px;height: 427px;}
.main .main_best ul li .cont_1{padding-bottom:20;height: 427px;border: 1px solid #f4f6f8;border-bottom: 1px solid #d0d1d3;background:#fff}
.main .main_best ul li .cont_1 img{width:280px;height:280px;margin-left:2px;}
.main .main_best ul li p{overflow: hidden;display: block;height: 37px;margin-right: -1px;padding-right: 1px;font-size: 14px;line-height: 18px;color: #333;word-break: keep-all;margin:15px}
.main .main_best ul li span{font: bold 22px/22px '';color: #333;margin:15px}

.main .main_hot{padding-top:45px;height:495px;position: relative;}
.main .main_hot .main_hot-t{display:flex;justify-content: space-around;height:112px;background:#fff;border-bottom:1px solid #aaa;box-sizing: border-box;margin-top:30px}
.main .main_hot .gd{float: right}
.main .main_hot .main_hot-t li{margin-top:25px;width:100px;}
main .main_hot .main_hot-t div{width:40px;height:40px;background:#f4f6f8 url(../images/icon.png) no-repeat;border-radius:50px;margin-left:20px}
main .main_hot .main_hot-t li:nth-child(1) div{background-position:-138px 0}
main .main_hot .main_hot-t li:nth-child(2) div{background-position:-138px -40px}
main .main_hot .main_hot-t li:nth-child(3) div{background-position:-138px -80px}
main .main_hot .main_hot-t li:nth-child(4) div{background-position:-138px -120px}
main .main_hot .main_hot-t li p{line-height: 16px;letter-spacing: 0.5px;font-weight: normal;padding-top:15px;font: 12px "";margin-left:20px}
main .main_hot .main_hot-b{padding: 20px;background: #fff;height:340px}
main .main_hot .main_hot-b li{float: left;padding: 0 4px;box-sizing: border-box}

main .main_hot .main_hot-b li .cont-1{width:223px;height:340px;background: #fff;position: relative;}
main .main_hot .main_hot-b li .cont-1 img{width:222px;margin-left:1px;}
main .main_hot .main_hot-b li .cont-1 p{overflow: hidden;display: block;height: 37px;margin-right: -1px;padding-right: 1px;font: 14px/18px "";color: #333;word-break: keep-all;margin:15px}
main .main_hot .main_hot-b li .cont-1 span{font: bold 22px/22px '';color: #333;margin:15px}
main .main_hot .main_hot-b li .cont-1 i{display:block;width:40px;height:40px;background:rgba(0,0,0,.4);position: absolute;border-radius:50%;text-align:center;font:16px/40px "";color:#fff;top:0}

main .main_hot .left1,.right1{width:60px;height:60px;position: absolute;z-index: 12;opacity: 0.7;border:none;}
main .main_hot .left1{left:0;top:300px;background:rgba(0,0,0,.4) url(../images/icon.png) no-repeat center;background-position: 0 -120px;}
main .main_hot .right1{right:0;top:300px;background:rgba(0,0,0,.4) url(../images/icon.png);background-position: 0 0;}



main .main_rec{padding-top:45px;height:326px;margin-top:30px}
main .main_rec>div{height:40px}
main .main_rec h3{font:28px/36px "";font-weight: bold;color: #2d323b;padding-bottom:15px;float: left;}
main .main_rec .tjzq{float:right;}
main .main_rec .tjzq a{height: 28px;width: 40px;padding: 0 17px;margin-left: -4px;line-height: 28px;color: #666;font-size: 14px;line-height: 28px;display: block;float: left;text-align: center;border-radius:14px}
main .main_rec .tjzq span{width: 5px;height: 5px;background: #666;display: inline-block;border-radius: 50%;display: block;float: left;margin:10px}


main .main_rec ul{height:244px;position: relative;padding:0 235px;border-bottom: 2px solid #d5d9db;top:30px}
main .main_rec ul li{float: left;border: .5px solid #e6ebee;width:240px;height:61px}
main .main_rec ul li img{width:240px;height:61px}
main .main_rec ul li:first-child{position: absolute;left:0;top:0;border:none}
main .main_rec ul li:first-child img{width:240px;height:243px;}
main .main_rec ul li:last-child{position: absolute;right:0;top:0;border:none}
main .main_rec ul li:last-child img{width:240;height:243px;}





.main .main_11s{margin-top:70px;overflow: hidden;background:#fff;}
.main .main_11s .logo3{float: left;}
.main .main_11s .a11s_r{float: left;padding:10px 20px 0 20px;height:315px;background:#fff;}
.main .main_11s .a11s_r .a11s_r_l{float: left}
.main .main_11s .a11s_r .a11s_r_c{float: left;margin-left:20px}
.main .main_11s .a11s_r .a11s_r_r{float: left;margin-left:20px}
.main .main_11s .a11s_r .shangfang{display:flex;justify-content:space-between;height: 40px;border-bottom: 1px solid #666}
.main .main_11s .a11s_r h3{font-size: 16px;line-height: 20px;font-weight: bold;color: #2d323b;}
.main .main_11s .a11s_r em{font-size: 14px;line-height: 18px;}
.main .main_11s .a11s_r p{padding-top:20px}
.main .main_11s .a11s_r p span{width: 5px;height: 5px;background: #666;display: inline-block;border-radius: 50%}
.main .main_11s .a11s_r p b{    display:inline-block;overflow: hidden;max-height: 36px;padding-left: 12px;font-size: 14px;line-height: 18px;margin: -5px;}
.main .main_11s .a11s_r .a11s_r_r img{display:block;margin-top:22px} 

main .main_pay{padding-top:35px;padding-top: 20px;}
